* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.form-container {
  margin: 20px 0 0 100px;
}

#search-box {
  width: 350px;
  color: #fff;
  letter-spacing: .2em;
  text-align: center;
  background: linear-gradient(45deg, #666, #444);
  border: none;
  border-radius: 10px;
  outline: none;
  padding: 20px 15px;
  font-size: 18px;
  font-style: italic;
}

#search-box::-ms-input-placeholder {
  color: rgba(255, 255, 255, .7);
  font-style: italic;
}

#search-box::placeholder {
  color: rgba(255, 255, 255, .7);
  font-style: italic;
}

#search-box:focus {
  outline: 2px solid rgba(0, 0, 0, .8);
}

.country-list {
  flex-direction: column;
  gap: 10px;
  padding: 0;
  display: flex;
}

.country-list-item {
  max-width: 320px;
  align-items: center;
  margin-left: 15px;
  display: flex;
}

.country-small-flag {
  width: 80px;
  height: 50px;
  display: block;
}

.country-small-text {
  width: 320px;
  color: #fff;
  letter-spacing: .11em;
  text-align: center;
  background: linear-gradient(45deg, #444, #777);
  border-radius: 10px;
  margin-left: 20px;
  padding: 10px 0;
  font-style: italic;
  display: inline-block;
}

.country-info {
  transition: transform .3s linear;
  transform: translateX(-200%);
}

.country-info.visible {
  width: 320px;
  background: linear-gradient(45deg, #444, #777);
  border-radius: 10px;
  margin-left: 15px;
  padding: 10px;
  transform: translateX(0);
}

.country-big-flag {
  width: 300px;
}

.country-name {
  text-align: center;
  color: rgba(255, 255, 255, .7);
  letter-spacing: .1em;
}

.country-title {
  color: #141414;
  letter-spacing: .1em;
  font-size: 20px;
  font-weight: 500;
}

.country-item {
  color: rgba(255, 255, 255, .7);
  font-size: 18px;
}

